import React, {Component} from 'react'
import {Form, Icon, Input, Button} from 'antd';

const FormItem = Form.Item;

class _Login extends Component {
    constructor(props) {
        super(props)
    }

    render() {
        const {getFieldDecorator} = this.props.form
        return (
            <Form className="signIn"> {/* */}
                <FormItem className="row">
                    {getFieldDecorator('userName-login', {
                        rules: [{required: true, message: ''}],
                    })(<Input type="text"
                              prefix={<Icon type="user"></Icon>}
                        // value={this.state.formData.username}
                              onChange={this.props.changeInfo.bind(null,'username')}
                              placeholder="" size="large"/>)}
                </FormItem>
                <FormItem className="row">
                    {getFieldDecorator('password-login', {
                        rules: [{required: true, min: 6, message: '6'}],
                    })(<Input type="password"
                              prefix={<Icon type="lock"></Icon>}
                              onChange={this.props.changeInfo.bind(null, 'password')}
                              placeholder=""
                              size="large"/>)}
                </FormItem>
                <div className="row actions">
                    <Button type="primary" style={{width: '390px'}}
                            onClick={this.props.handleSubmit.bind(null, 'signIn')}></Button>
                </div>
                <div className="swtichLoginAndRegister">
                    <div>
                        <span onClick={this.props.switch.bind(this, false)} style={{cursor: 'pointer', color: '#1890ff'}}></span>
                    </div>
                    <div>
                        <span style={{cursor: 'pointer', color: '#1890ff'}} onClick={this.props.toForgetPassword}></span>
                    </div>
                </div>
            </Form>
        )
    }
}

const Login = Form.create()(_Login)
export default Login


